import uniLoadMore from '@/components/uni-load-more.vue';
	export default { 
		 components: {
		        uniLoadMore
		    },
		data() {
			 return{
				  loadingType: 0,//定义加载方式 0---contentdown  1---contentrefresh 2---contentnomore
				  contentText: {
				        contentdown:'上拉显示更多',
				        contentrefresh: '正在加载...',
				        contentnomore: '没有更多数据了'
				  },  
				  scrollTop: 0,
				  old: {
				     scrollTop: 0
				 },
				 fileMapper:{}, 
				 selectArchArr:[],
				 
				 fkStoredId:'',
				 fkFileName:'',
				 // 分页
				 page:{
					 currentPage:1,
					 pageSize:5,
					 totalPage:0
				 }
			 }
		},
		onLoad(option) {   
			let _this=this;
			uni.getStorage({
			    key: 'fkStoredId',
			    success: function (res) {
			        _this.fkStoredId=res.data;
			    }
			});
			_this.fkFileName=option.fkFileName;
			_this.selectArchList(option.id);	 
		},	
		// 下拉刷新
		onReachBottom() { 
		    let _this=this; 
			if(_this.page.currentPage<=_this.page.totalPage){
				_this.page.currentPage++;
				_this.selectArch();
			}else{ 
				_this.loadingType = 2;
			}
			console.log(_this.page.totalPage)
		},
		methods: { 
			scroll: function(e) {
				console.log(e)
				this.old.scrollTop = e.detail.scrollTop
			},
					
			selectArchList: function(id){
				let _this=this; 
				if(_this.fkFileName){
					_this.selectArch();
				}
				if(id){
					_this.submitAjax('appmodule/appFileMapper/selectAppById?id='+id,null,'GET',function(res){ 
						console.log(res)
						if(res.data.state){   
							_this.selectArchArr.push(res.data.row);
							_this.page.totalPage=1;
						}
					})
				}
			}, 
			// 根据关键字查询
			selectArch:function(){
				let _this=this; 
				var data={
					fkFileName:_this.fkFileName,
					fkStoredId:_this.fkStoredId,
					currentPage:_this.page.currentPage,
					pageSize:_this.page.pageSize
				}; 
				_this.submitAjax('appmodule/appFileMapper/selectAppByName', data, 'GET',
					function(res) {
						if(res.data.state){
							_this.page.totalPage=res.data.page;  
							_this.selectArchArr=_this.selectArchArr.concat(res.data.rows); 
						}
					})
			},
			// 打开架体
			openshelf:function(localId) { 
				let _this = this;
				var data={};
				_this.submitAjax('storeroommodule/stoTbRegion/selectOpenMJJById', { localId }, 'GET',
					function(res) { 
						if (res.data.state && res.data.row){
							var msg=res.data.row;
							var locate=msg.location;
							var resjson = msg.region;
							var dir=locate.direction;
							var cols=locate.colNum;
							var gdlType;
							if(resjson.gdlType==="left"){
								gdlType="left";
								if(dir==="右"){
									cols++;
								}
							}else if(resjson.gdlType==="right"){
								gdlType="right";
								if(dir==="左"){
									cols--;
								}
							}  
							_this.submitAjax(_this.$mjjUrl+'denseShelves/openframe?quNum='+locate.fkRegionNum+'&column='
							+cols+'&section='+locate.divNum+'&layer='+locate.laysNum+'&direction='+gdlType,null, 'POST', function(res) { 
									uni.showToast({
										icon:'loading',
									    title: '打开架体中...',
									    duration: 1000
									});	
								if(res.data.state){
									uni.showToast({
										icon:'none',
									    title: '打开架体成功',
									    duration: 2000
									});	
								}else{
									uni.showToast({
										icon:'none',
									    title: '打开架体失败',
									    duration: 2000
									});	
								} 
								console.log(res)
							});
						}  
					})
		
			}
		}
   }	
